<template>

	<div>
		<div class='creat'>
			<a-row>
				<a-col>
					<a-input-search placeholder="用户名" style="width: 200px" @search="onSearch" />

					<a-button type="primary" icon="plus-circle" @click='showDrawer' style='float: right;'>
						添加用户
					</a-button>
					<a-drawer title="添加用户" placement="right" :closable="false" :visible="visible" width='300px'
						:after-visible-change="afterVisibleChange" @close="onClose">

						<a-form :form="form" layout='horizontal'>
							<a-form-item label="用户名">
								<a-input v-decorator="['name', { rules: [{ required: true, message: '请输入用户名!' }] }]" />
							</a-form-item>
							<a-form-item label="密码">


								<a-input v-decorator="[
						          'password',
						          { rules: [{ required: true, message: '请输入密码!' },{ min: 8, message: '密码少于8位' }] },
						        ]" type='password'>
								</a-input>
							</a-form-item>

							<a-form-item label="昵称">


								<a-input v-decorator="[
							      'alisname',
							      { rules: [{ required: true, message: '请输入昵称!' }] },
							    ]">
								</a-input>
							</a-form-item>

						</a-form>

						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose">
								取消
							</a-button>
							<a-button type="primary" @click="handleSubmit">
								确定
							</a-button>
						</div>
					</a-drawer>
					
					
					
					
					<a-drawer title="修改用户" placement="right" :closable="false" :visible="visible_Edit" width='300px'
						:after-visible-change="afterVisibleChange" @close="onCloseEdit">
					
						<a-form :form="formEdit" layout='horizontal'>
							<a-form-item label="用户名" >
								<a-input v-decorator="['name', { rules: [{ required: true, message: '请输入用户名!' }] }]"  disabled/>
							</a-form-item>
							<a-form-item label="密码">
					
					
								<a-input v-decorator="[
						          'password',
						          { rules: [{ required: true, message: '请输入密码!' },{ min: 8, message: '密码少于8位' }] },
						        ]" type='password'>
								</a-input>
							</a-form-item>
					
							<a-form-item label="昵称">
					
					
								<a-input v-decorator="[
							      'alisname',
							      { rules: [{ required: true, message: '请输入昵称!' }] },
							    ]">
								</a-input>
							</a-form-item>
					
						</a-form>
					
						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose_edit">
								取消
							</a-button>
							<a-button type="primary" @click="UpdateSubmit">
								更新
							</a-button>
						</div>
					</a-drawer>

				</a-col>


			</a-row>

		</div>
		<div id='list'>
			<a-row>
				<a-table :columns="columns" :data-source="data" :row-key="record => record.id" :pagination="pagination"
					:loading="loading" @change="" size="small">


					<div slot='name' slot-scope="text,record,index">
						<a-tag color="red">
							{{record.name}}
						</a-tag>
					</div>

					<span slot="edit" slot-scope="text, record">
						<a-space size="small">
							<a-tooltip placement="left">
								<template slot="title">
									<span>删除</span>
								</template>
								<a-popconfirm title="是否要删除?" ok-text="是" cancel-text="否" @confirm="del(record.id)">
									<a-icon class="pointer" type="delete" />
								</a-popconfirm>
							</a-tooltip>
							<span>|</span>

							<a-tooltip placement="top">
								<template slot="title">
									<span>编辑</span>
								</template>
								<a-icon class="pointer" type="form" @click="showEdit(record.id,record.name,record.alisname)" />
							</a-tooltip>

						</a-space>
					</span>

				</a-table>

			</a-row>
		</div>

	</div>
</template>

<script>
	const columns = [{
			title: '用户名',
			dataIndex: 'name',
			width: '20%',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '昵称',
			dataIndex: 'alisname',
			width: '20%',
		},
		{
			title: '操作',
			dataIndex: 'edit',
			align: 'center',
			scopedSlots: {
				customRender: 'edit'
			},
		},
	];


	export default {
		data() {
			return {
				columns,
				loading: false,
				data: [],
				pagination: {
					// total: this.total,
					defaultPageSize: 10,
					// pageSize.sync:'5'
					showQuickJumper: true,
					showSizeChanger: true,
					pageSizeOptions: ['10', '20', '30', '40', '50', '100'],

				},
				visible: false,
				visible_Edit:false,
				form: this.$form.createForm(this),
				formEdit:this.$form.createForm(this),

			}
		},
		methods: {
			onload() {
				this.loading = true
				this.$http.get('/api/user/list/').then((res) => {
					this.loading = false
					this.data = res.data.res
					console.log(res)
				})

			},
			onSearch(value) {
				this.$http.post('/api/user/search/',{
					'result':value
				}).then((res)=>{
					this.data = res.data.res
					this.$message.success('查询成功')
				})
				

			},
			showEdit(id,name,alisname) {
				this.visible_Edit = true
				setTimeout(() => {
					this.formEdit.setFieldsValue({
						name: name,
						alisname: alisname,
					});
				}, 100);
			},

			del(id) {
				this.$http.post('/api/user/del/',{
					id:id,
				}).then((res)=>{
					if (res.data.status === 'success') {
						this.$message.success('删除成功')
					}
					if (res.data.status === 'fail') {
						this.$message.error('内置用户不允许删除')
					}
					this.onload()
				})
			},
			afterVisibleChange(val) {
				console.log('visible', val);
			},
			showDrawer() {
				this.visible = true
			},
			onClose() {
				this.visible = false
			},
			onCloseEdit(){
				this.visible_Edit = false
				
			},
			onClose_edit(){
				this.visible_Edit = false
			},
			handleSubmit() {
				this.form.validateFields((err, values) => {
					if (!err) {
						this.$http.post('/api/user/add/', {
							'username': values.name,
							'alisname': values.alisname,
							'password': values.password,
						}).then((res) => {
							console.log(res)
							if (res.data.msg === '账户已存在') {
								this.$message.error('账户已存在')
							}
							if (res.data.status === 'success') {
								this.$message.success('添加成功')
							}
							this.visible = false
							this.onload()
						})

					}
				})
			},
			UpdateSubmit(){
				this.formEdit.validateFields((err, values) => {
					if(!err){
						this.$http.post('/api/user/edit/',{
							'name' : values.name,
							'password':values.password,
							'alisname':values.alisname,
							
						}).then((res)=>{
							if (res.data.status === 'success') {
								this.$message.success('更新成功')
							}
							this.visible_Edit = false
							this.onload()
						})
					}
				})
				
			},



		},
		mounted() {
			this.onload()
		},

	}
</script>

<style scoped>
	.creat {
		background: #edeff3;
		border: 8px solid #edeff3;
		border-radius: 5px;
	}

	#list {
		margin-top: 10px;
	}
</style>
