<template>
	<div>
		<div class="head">
			
			<a-input-search placeholder="关键字" style="width: 200px" @search="onSearch" />
		</div>
		<div class="list">
			<a-row>
				<a-table :columns="columns" :data-source="data" :row-key="record => record.id" :loading="loading"
					@change="" size="small" :pagination="pagination">

					<div slot='url' slot-scope="text,record,index">
						<a :href="record.url" target="view_window">{{record.url}}</a>
					</div>

					<div slot='one_md5' slot-scope="text,record,index">
						<a-tag color="purple">
							{{record.one_md5}}
						</a-tag>
					</div>
					<div slot='two_md5' slot-scope="text,record,index">
						<a-tag color="purple">
							{{record.two_md5}}
						</a-tag>
					</div>
					<div slot='new_page' slot-scope="text,record,index">
						<a v-if="!record.new_page"  >
							<a-tag color="cyan">
								null
							</a-tag>
						</a>

						<a :href="record.new_page" target="view_window" v-else >
							<a-tag color="red">
								快照
							</a-tag>
						</a>





					</div>

					<div slot='code' slot-scope="text,record,index">
			
						<a v-if="!record.code">
							<a-tag color="cyan">
								null
							</a-tag>
						</a>

						<a :href="record.code" target="view_window" v-else>
							<a-tag color="red">
								源码
							</a-tag>
						</a>
					</div>

					<div slot='ky_count' slot-scope="text,record,index">
						<a v-if="!record.ky_count">
							<a-tag color="cyan">
								null
							</a-tag>
						</a>
						<a :href="record.ky_count" target="view_window" v-else>
							<a-tag color="red">
								对比报告
							</a-tag>
						</a>

					</div>

				</a-table>

			</a-row>
		</div>


	</div>


</template>

<script>
	const columns = [

		{
			title: 'URL',
			dataIndex: 'url',
			scopedSlots: {
				customRender: 'url'
			},
			width: '20%',
		},

		{
			title: '初始MD5值',
			dataIndex: 'one_md5',
			scopedSlots: {
				customRender: 'one_md5'
			},
		},
		{
			title: '现MD5值',
			dataIndex: 'two_md5',
			scopedSlots: {
				customRender: 'two_md5'
			},

		},
		{
			title: '篡改页面',
			dataIndex: 'new_page',
			scopedSlots: {
				customRender: 'new_page'
			},
		},
		{
			title: '篡改代码',
			dataIndex: 'code',
			align: 'code',
			scopedSlots: {
				customRender: 'code'
			},
		},
		{
			title: '代码校对',
			dataIndex: 'ky_count',
			scopedSlots: {
				customRender: 'ky_count'
			},

		},
		// {
		// 	title: '次数',
		// 	dataIndex: 'wz_count',

		// },


	];


	// const data = [{
	//     'url': 'www.baidu.com',
	//     'title': 'baidu',
	//     'MD5': 'adsfja123123kljksjdf',
	//     'status': ['可用', '完整',],
	//     'id': 1
	//
	// },]
	export default {
		data() {
			return {
				pageSizeOptions: ['10', '20', '30', '40', '50', '6000000000000000000'],
				current: 1,
				pagination: {
					// total: this.total,
					defaultPageSize: 10,
					// pageSize.sync:'5'
					showQuickJumper: true,
					showSizeChanger: true,
					pageSizeOptions: ['10', '20', '30', '40', '50', '100'],

				},
				columns,
				loading: false,
				visible: false,
				data: [],
			}
		},
		methods: {
			onShowSizeChange(current, pageSize) {
				this.pageSize = pageSize;
			},
			onChange(page, pageSize) {
				console.log(page, pageSize)
			},
			handleChange(value) {
				console.log(value)

			},
			onSearch(value) {
				this.$http.post('/api/report/search/',{
					cxdata:value,
				}).then((res)=>{
					this.$message.success('查询成功')
					this.data=res.data.res
				})
			},

			onLoad() {
				this.loading = true;
				this.$http.get('/api/report/list').then((res) => {
					console.log(res);
					this.loading = false;
					this.data = res.data.res;
					// this.total = int(res.data.total)

				})
			},

		},
		mounted() {
			this.onLoad()
		},
	}
</script>

<style scoped>
	.tp {
		margin-bottom: 10px;
		/*border: 2px solid beige ;*/
	}

	.fenye {
		float: right;
		margin-top: 40px;
	}

	.head {
		background-color: #edeff3;
		border: solid 8px #edeff3;
		border-radius: 5px;
	}

	.list {
		margin-top: 10px;
	}

	.xiangqing {
		max-height: 200px;
		max-width: 500px;
		overflow: auto;
	}

	.img_dx {
		width: 400px;
		height: 400px;
	}
</style>
