<template>
	<a-layout id="components-layout-demo-custom-trigger">
		<a-layout-sider v-model="collapsed" :trigger="null" collapsible>
			<div class="logo" v-if='!collapsed'>
				<img class='box' src="../assets/hild.png" alt="">

			</div>
			<div class="limt" v-else>
				<a-avatar icon="chrome" style='backgroundColor:#87d068'>

				</a-avatar>
			</div>
			<a-menu theme="dark" mode="inline" :default-selected-keys="['']" @click='click'>
				<a-menu-item key="/dashboard">
					<a-icon type="dashboard" />
					<span>首 页</span>
				</a-menu-item>
				<a-menu-item key="/task">
					<a-icon type="video-camera" />
					<span>任务监测</span>
				</a-menu-item>
				<a-menu-item key="/status">
					<a-icon type="dot-chart" />
					<span>状态概览</span>
				</a-menu-item>
				<a-menu-item key="/report">
					<a-icon type="file-done" />
					<span>监测报告</span>
				</a-menu-item>
				<a-menu-item key="/user">
					<a-icon type="user" />
					<span>用户管理</span>
				</a-menu-item>
				<a-menu-item key="/system">
					<a-icon type="setting" />
					<span>系统设置</span>
				</a-menu-item>
			</a-menu>
		</a-layout-sider>
		<a-layout>
			<a-layout-header style="background: #fff; padding: 0">
				<a-icon class="trigger" :type="collapsed ? 'menu-unfold' : 'menu-fold'"
					@click="() => (collapsed = !collapsed)" />
				<a-dropdown :trigger="['click']" class='logout'>
					<a class="ant-dropdown-link" @click="e => e.preventDefault()">
						<a-tag color="#f50">
							{{user}}
						</a-tag>
						<a-icon type="down" />
					</a>
					<a-menu slot="overlay">
						<a-menu-item key="0">
							<a href="javascript:;" @click="showModal">
								<a-icon type="lock" />
								&nbsp;修改密码
							</a>
							<div>

								<a-modal title="修改密码" :visible="visible" :confirm-loading="confirmLoading"
									@ok="handleOk" @cancel="handleCancel" width='300px'>
									<a-form :form="form">
										<a-form-item>
											<a-input disabled v-decorator="[
										          'username',
										          { rules: [{ required: true,}] },
										        ]">
												
												<a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
											</a-input>
										</a-form-item>
										<a-form-item>
											<a-input v-decorator="[
										          'password',
										          { rules: [{ required: true, message: '请输入密码!' },{ min: 8, message: '密码少于8位' }] },
										        ]" type="password" placeholder="Password">
												<a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
											</a-input>
										</a-form-item>


									</a-form>

								</a-modal>
							</div>
						</a-menu-item>

						<a-menu-item key="1">
							<a href="javascript:;" @click='logout'>
								<a-icon type="logout" />
								&nbsp;退出登录
							</a>
						</a-menu-item>
					</a-menu>
				</a-dropdown>
			</a-layout-header>
			<a-layout-content :style="{ margin: '24px 16px', padding: '24px', background: '#fff', minHeight: 'auto', }">
				<router-view></router-view>
			</a-layout-content>
		</a-layout>
	</a-layout>
</template>

<script>
	import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN';
	export default {
		data() {
			return {
				collapsed: false,
				user: '',
				visible: false,
				confirmLoading: false,
				form: this.$form.createForm(this),

			};
		},
		methods: {
			click(e) {
				console.log(e)
				this.$router.push(e.key)
			},
			logout() {
				window.sessionStorage.clear()
				this.$router.push('/login')
			},
			showModal() {
				this.visible = true
				setTimeout(() => {
					this.form.setFieldsValue({
						username:this.$store.state.loginuser,
					});
				}, 100);
			},
			handleCancel() {
				this.visible = false
			},
			handleOk() {
				this.form.validateFields((err, values) => {
					if(!err){
						this.$http.post('/api/user/edit/',{
							'name' : values.username,
							'password':values.password,
						}).then((res)=>{
							if(res.data.status=='success'){
								this.$message.success('修改成功')
								this.visible=false
								
							}
						})
					}
				})
				
			},
			
		},

	mounted() {
		this.user = this.$store.state.loginuser;
	}
	};
</script>

<style scoped>
	.ant-layout {
		height: 100%;
	}

	#components-layout-demo-custom-trigger .trigger {
		font-size: 18px;
		line-height: 64px;
		padding: 0 24px;
		cursor: pointer;
		transition: color 0.3s;
	}

	#components-layout-demo-custom-trigger .trigger:hover {
		color: #1890ff;
	}

	#components-layout-demo-custom-trigger .logo {
		height: 80px;
		/* background: rgba(255, 255, 255, 0.2); */
		margin: 16px;
		text-align: center;
		color: white;

	}

	/*.ant-layout-content{*/
	/*min-height: auto;*/
	/*}*/

	.title {
		height: 32px;
		text-align: center;
		color: #D9D9D9;
		margin: 16px;
	}

	#jc {
		margin: 10px;
		color: #FFFFFF;
	}

	.limt {
		height: 32px;
		margin: 16px;
		text-align: center;
	}

	.box {
		height: 80px;
		width: 80px;
		border-radius: 50px;


	}

	.logout {
		float: right;
		margin-right: 18px;

	}
</style>
