import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Index from '../views/Index.vue'
import Dashboard from '../views/Dashboard.vue'
import Task from '../views/Task.vue'
import Error from '../views/404.vue'
import Page from '../views/Page.vue'
import Status from '../views/Status'
import test from '../views/test'
import Report from '../views/Report'
import System from '../views/System'
import User from '../views/User.vue'
Vue.use(VueRouter)

const routes = [{
		path: '/login',
		name: 'Login',
		component: Login
	}, {
		path: '/',
		redirect: '/login'
	},
	{
		path: '/index',
		name: 'Index',
		component: Index,
		children: [{
				path: '/dashboard',
				name: 'Dashboard',
				component: Dashboard
			},
			{
				path: '/task',
				name: 'Task',
				component: Task
			},
			{
				path: '/page',
				name: 'Page',
				component: Page
			},
			{
				path: '/status',
				name: 'Status',
				component: Status
			},
			{
				path: '/test',
				name: 'test',
				component: test
			},
			{
				path: '/report',
				name: 'Report',
				component: Report
			},
			{
				path: '/system',
				name: 'System',
				component: System
			},
			{
				path: '/user',
				name: 'User',
				component: User
			},
		]
	},
	{
		path: '*',
		name: '404',
		component: Error
	}


]



const router = new VueRouter({
	routes
})

