<template>
	<div>
		<div class="head">
			<a-select default-value="全部" style="width: 120px" @change="handleChange">
				<a-select-option value="all">
					全部
				</a-select-option>
				<a-select-option value="availability">
					不可用
				</a-select-option>
				<a-select-option value="integrity">
					不完整
				</a-select-option>
			</a-select>
			<a-input-search placeholder="关键字" style="width: 200px" @search="onSearch" />
			<div style="float: right;">
				<strong style="color:#6dc7e8;margin-right: 5px;padding: 5px;border: solid 1px;">定时刷新</strong>
				<a-select default-value="关闭" style="width: 80px" @change="shuaxin">
					<a-select-option value="0">
						关闭
					</a-select-option>
					<a-select-option value="10">
						10秒
					</a-select-option>
					<a-select-option value="15">
						15秒
					</a-select-option>
					<a-select-option value="20">
						20秒
					</a-select-option>
				</a-select>
			</div>

		</div>
		<div class="list">
			<a-row>
				<a-table :columns="columns" :data-source="data" :row-key="record => record.id" :loading="loading"
					@change="" size="small" :pagination="pagination">

					<div slot='url' slot-scope="text,record,index">
						<a :href="record.url" target="view_window">{{record.url}}</a>
					</div>

					<div slot='MD5' slot-scope="text,record,index">
						<a-tag color="purple">
							{{record.MD5}}
						</a-tag>
					</div>
					<div slot='status' slot-scope="text,record,index">
						<a-tag color="green" v-if="record.status[0]==='可用'">
							{{record.status[0]}}
						</a-tag>
						<a-tag color="red" v-else>
							{{record.status[0]}}
						</a-tag>
						<a-tag color="green" v-if="record.status[1]==='完整'">
							{{record.status[1]}}
						</a-tag>
						<a-tag color="red" v-else>
							{{record.status[1]}}
						</a-tag>

					</div>
					<div slot='xsd' slot-scope="text,record,index">
						<a-tag color="red" >
							{{record.xsd}}%
						</a-tag>
					</div>
					<div slot='id' slot-scope="text,record,index">
						<a-button type="primary" size="small" @click='showDrawer(record.id)'>
							详情
						</a-button>
					</div>
				</a-table>

			</a-row>
		</div>

		<div>
			<a-drawer title="监测详情" placement="right" :closable="false" :visible="visible" width='500px'
				@close="onClose">

				<!--这是抽屉详细内容-->
				<div>
					<a-descriptions bordered size="small" layout="vertical" :column="2" v-if="visible">
						<a-descriptions-item label="Url">
							<a :href="datavalue.url" target="view_window">{{datavalue.url}}</a>

						</a-descriptions-item>

						<a-descriptions-item label="状态">

							<a-tag color="green" v-if="datavalue.status[0]==='可用'">
								{{datavalue.status[0]}}
							</a-tag>
							<a-tag color="red" v-else>
								{{datavalue.status[0]}}
							</a-tag>
							<a-tag color="green" v-if="datavalue.status[1]==='完整'">
								{{datavalue.status[1]}}
							</a-tag>
							<a-tag color="red" v-else>
								{{datavalue.status[1]}}
							</a-tag>

						</a-descriptions-item>
						<a-descriptions-item label="MD5" :span="2">

							<a-tag color="purple">
								{{datavalue.md5_jc}}
							</a-tag>

							<a-popconfirm title="是否同步?" ok-text="是" cancel-text="否" @confirm="clicksync(datavalue.id)">
								<a-button type="danger" size="small">
									同步
								</a-button>
							</a-popconfirm>


						</a-descriptions-item>

						<a-descriptions-item label="可用性时间轴">
							<a-timeline class="xiangqing">
								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>
								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>
								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>

								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>

							</a-timeline>
						</a-descriptions-item>
						<a-descriptions-item label="完整性时间轴">
							<a-timeline class="xiangqing">
								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>
								<a-timeline-item color="red">
									Create a services site 2015-09-01
								</a-timeline-item>

							</a-timeline>
						</a-descriptions-item>
						<a-descriptions-item label="header" :span="2">
							<pre class="xiangqing">{{datavalue.header}}</pre>
						</a-descriptions-item>
						<a-descriptions-item label="快照" :span="2">
							<img :src="datavalue.img" alt="" class="img_dx" @click="upimg">
							<div>
								<a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel" width="1000px">
									<img alt="example" style="width: 100%;" :src="datavalue.img" />
								</a-modal>
							</div>
						</a-descriptions-item>
					</a-descriptions>
				</div>


				<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
					<a-button style="marginRight: 8px" @click="onClose">
						取消
					</a-button>
					<a-button type="primary">
						确定
					</a-button>
				</div>
			</a-drawer>
		</div>


	</div>


</template>

<script>
	const columns = [

		{
			title: 'URL',
			dataIndex: 'url',
			scopedSlots: {
				customRender: 'url'
			},
			// width: '20%',
		},
		{
			title: '标题',
			dataIndex: 'title',
		},

		{
			title: 'MD5值',
			dataIndex: 'MD5',
			scopedSlots: {
				customRender: 'MD5'
			},
		},
		{
			title: '状态',
			dataIndex: 'status',
			scopedSlots: {
				customRender: 'status'
			},

		},
		{
			title: '相似度',
			dataIndex: 'xsd',
			scopedSlots: {
				customRender: 'xsd'
			},
		
		},
		{
			title: '详情',
			dataIndex: 'id',
			align: 'id',
			scopedSlots: {
				customRender: 'id'
			},
		},

	];


	// const data = [{
	//     'url': 'www.baidu.com',
	//     'title': 'baidu',
	//     'MD5': 'adsfja123123kljksjdf',
	//     'status': ['可用', '完整',],
	//     'id': 1
	//
	// },]
	export default {
		data() {
			return {
				pageSizeOptions: ['10', '20', '30', '40', '50', '6000000000000000000'],
				current: 1,
				pagination: {
					// total: this.total,
					defaultPageSize: 10,
					// pageSize.sync:'5'
					showQuickJumper: true,
					showSizeChanger: true,
					pageSizeOptions: ['10', '20', '30', '40', '50', '100'],

				},
				columns,
				loading: false,
				visible: false,
				data: [],
				datavalue: {
					id: '',
					url: '',
					MD5: '',
					header: '',
					title: '',
					status: '',
					img: '',
					md5_jc: ''
				},
				previewVisible: false,
				time: '',
				dstime: '',
				year: '',
				intervalId: null,
				valId:null,
				

			}
		},
		methods: {
			onShowSizeChange(current, pageSize) {
				this.pageSize = pageSize;
			},
			onChange(page, pageSize) {
				console.log(page, pageSize)
			},
			handleChange(value) {
				this.$http.post('/api/liststatus/search/',{
					cxdata:value,
				}).then((res)=>{
					if (res.data.status == 'success') {
						this.data=res.data.res
						
					
					}
					
					
				})
				
				console.log(value)

			},
			shuaxin(value) {
				if(value != '0'){
					this.time = value
					clearTimeout(this.intervalId)
					this.valId = null
					this.intervalId=null
					console.log('刷新时间修改为' + this.time)
					this.year = new Date()
				}else{
					this.valId = 'nulll'
					this.intervalId=null
				}
				
				
			},

			onSearch(value) {
				this.$http.post('/api/liststatus/search/',{
					cxdata:value,
				}).then((res)=>{
					if (res.data.status == 'success') {
						this.$message.success('查询成功')
						this.data=res.data.res
						
					
					}
					
					
				})
			},
			onClose() {
				this.visible = false;
			},
			showDrawer(value) {
				this.visible = true;
				this.$http.post('/api/liststatus/xq', {
					id: value,
				}).then((res) => {
					
					this.datavalue = res.data.res;
				})


			},
			onLoad() {
				this.loading = true;
				this.$http.get('/api/liststatus/').then((res) => {
					this.loading = false;
					this.data = res.data.res;
					// this.total = int(res.data.total)

				})
			},
			upimg() {
				this.previewVisible = true
			},
			handleCancel() {
				this.previewVisible = false
			},
			clicksync(id) {
				this.$http.post('/api/liststatus/edit', {
					'id': id
				}).then((res) => {
					if (res.data.status == 'success') {
						this.$message.success('同步成功')
						this.visible = false;
						this.onLoad()

					}
				})

			},
			times() {
				console.log(this.valId,this.intervalId)
				// 未进行任务,执行
				if (this.valId == null) {
					this.intervalId = setTimeout(() => {
						this.onLoad()
						this.year=Date()
						this.valId == null
						this.dstime = parseInt(this.time) * 1000
						console.log(this.dstime)
					}, this.dstime)
				} else {
					console.log('不执行')
				}
			},

		},
		watch: { //监听，监听什么？我们在data中定义一个变量，就监听这个变量

			year() { //被监听的变量，也可以是对象集合等
				//当year中的值变化时，我们就触发定时事件
				this.times()

			}
		},
		mounted() {
			this.onLoad()
		},
		destroyed() { //销毁是不能忘记的
			clearTimeout(this.intervalId)
		}
	}
</script>

<style scoped>
	.tp {
		margin-bottom: 10px;
		/*border: 2px solid beige ;*/
	}

	.fenye {
		float: right;
		margin-top: 40px;
	}

	.head {
		background-color: #edeff3;
		border: solid 8px #edeff3;
		border-radius: 5px;
	}

	.list {
		margin-top: 10px;
	}

	.xiangqing {
		max-height: 200px;
		max-width: 500px;
		overflow: auto;
	}

	.img_dx {
		width: 400px;
		height: 400px;
	}
</style>
