<template>
	<div>
		<div class='creat'>
			<a-row>
				<a-col :span="12">
					<a-input-search placeholder="搜索任务" style="width: 200px" @search="onSearch" />
				</a-col>
				<a-col class='cj'>
					<a-button type="primary" icon="plus-circle" @click='showDrawer'>
						创建任务
					</a-button>
					<a-drawer title="创建任务" placement="right" :closable="false" :visible="visible" width='300px'
						:after-visible-change="afterVisibleChange" @close="onClose">

						<a-form :form="form" layout='horizontal'>
							<a-form-item label="任务名称">
								<a-input v-decorator="['name', { rules: [{ required: true, message: '请输入任务名称!' }] }]" />
							</a-form-item>
							<a-form-item>
								<span slot="label">
									域名
									<a-tooltip title="输入格式http://xxx.com或者https://xx.com:8080">
										<a-icon type="question-circle-o" />
									</a-tooltip>
								</span>


								<a-input v-decorator="[
						          'domain',
						          { rules: [{ required: true, message: '请输入域名!' }] },
						        ]">
								</a-input>

								<div style="margin-top: 20px;">
									立即执行
									<a-switch class='butt' @click="lj($event)">
										<a-icon slot="checkedChildren" type="check" />
										<a-icon slot="unCheckedChildren" type="close" />
									</a-switch>
								</div>

							</a-form-item>
							<a-divider orientation="left">告警邮箱</a-divider>
							<a-form-item>
								<a-input v-decorator="[
							          'email',
							          {
							            rules: [
							              {
							                type: 'email',
							                message: '邮箱格式错误!',
							              },
							              {
							                required: true,
							                message: '请输入邮箱!',
							              },
							            ],
							          },
							        ]" />
							</a-form-item>
							<a-divider orientation="left">告警范围</a-divider>
							<a-form-item>
								<a-checkbox-group v-model="value" name="checkboxgroup" :options="plainOptions"
									@change="onChange" />
							</a-form-item>
							<a-divider orientation="left">告警阀值</a-divider>
							<a-form-item>
								篡改相似度
								< <a-input-number id="inputNumber" v-model="xsnumber" :min="1" :max="100" />
								<div style="color: red;">
									设定阀值为100,代码一经变更触发告警
								</div>
							</a-form-item>




						</a-form>

						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose">
								取消
							</a-button>
							<a-button type="primary" @click="handleSubmit">
								确定
							</a-button>
						</div>
					</a-drawer>


					<a-drawer title="更新任务" placement="right" :closable="false" :visible="visible_edit" width='300px'
						:after-visible-change="afterVisibleChange" @close="onClose_edit">

						<a-form :form="formupdate" layout='vertical'>
							<a-form-item label="任务名称">
								<a-input v-decorator="['name', { rules: [{ required: true, message: '请输入任务名称!' }] }]" />
							</a-form-item>
							<a-form-item label="域名">
								<a-input v-decorator="[
						          'domain',
						          { rules: [{ required: true, message: '请输入域名!' }] },
						        ]">
								</a-input>
							</a-form-item>
							<a-divider orientation="left">邮箱修改</a-divider>
							<a-form-item label="告警邮箱">
								<a-input v-decorator="[
							         'email',
							         {
							           rules: [
							             {
							               type: 'email',
							               message: '邮箱格式错误!',
							             },
							             {
							               required: true,
							               message: '请输入邮箱!',
							             },
							           ],
							         },
							       ]" />
								   
							</a-form-item>
								<a-divider orientation="left">告警范围</a-divider>
								<a-form-item>
									<a-checkbox-group v-model="value" name="checkboxgroup" :options="plainOptions"
										@change="onChange" />
								</a-form-item>
								<a-divider orientation="left">告警阀值</a-divider>
								<a-form-item>
									篡改相似度
									< <a-input-number id="inputNumber" v-model="xsnumber" :min="1" :max="100" />
									<div style="color: red;">
										设定阀值为100,代码一经变更触发告警
									</div>
								</a-form-item>
								



						</a-form>

						<div :style="{
						          position: 'absolute',
						          bottom: 0,
						          width: '100%',
						          borderTop: '1px solid #e8e8e8',
						          padding: '10px 16px',
						          textAlign: 'right',
						          left: 0,
						          background: '#fff',
						          borderRadius: '0 0 4px 4px',
						        }">
							<a-button style="marginRight: 8px" @click="onClose_edit">
								取消
							</a-button>
							<a-button type="primary" @click="Editupdate">
								更新
							</a-button>
						</div>
					</a-drawer>

					<a-drawer title="任务详情" placement="right" :closable="false" :visible="visible_see" width='300px'
						:after-visible-change="afterVisibleChange" @close="onClose_see">

						<a-form :form="formupdate" layout='vertical'>
							<a-form-item label="任务名称">
								<a-input v-decorator="['name', { rules: [{ required: true, message: '请输入任务名称!' }] }]" />
							</a-form-item>
							<a-form-item label="域名">
								<a-input v-decorator="[
						          'domain',
						          { rules: [{ required: true, message: '请输入域名!' }] },
						        ]">
								</a-input>
							</a-form-item>
							<a-divider orientation="left">邮箱</a-divider>
							<a-form-item label="告警邮箱">
								<a-input v-decorator="[
							         'email',
							         {
							           rules: [
							             {
							               type: 'email',
							               message: '邮箱格式错误!',
							             },
							             {
							               required: true,
							               message: '请输入邮箱!',
							             },
							           ],
							         },
							       ]" />
							</a-form-item>
							</a-form-item>
							<a-divider orientation="left">告警范围</a-divider>
							<a-form-item>
								<a-checkbox-group v-model="value" name="checkboxgroup" :options="plainOptions"
									@change="onChange" />
							</a-form-item>
							<a-divider orientation="left">告警阀值</a-divider>
							<a-form-item>
								篡改相似度
								< <a-input-number id="inputNumber" v-model="xsnumber" :min="1" :max="100" />
								
							</a-form-item>



						</a-form>



					</a-drawer>

				</a-col>
			</a-row>
		</div>
		<div id='list'>

			<a-row>
				<a-table :columns="columns" :data-source="data" :row-key="record => record.id" :pagination="pagination"
					:loading="loading" @change="" size="small" bordered>


					<div slot='domain' slot-scope="text,record,index">
						<a :href="record.domain" target="view_window">{{record.domain}}</a>
					</div>


					<div slot='status' slot-scope="text,record,index" v-if='text==="1"'>
						<a-switch checked-children="开" un-checked-children="关" @click='qh($event,record.id)'
							default-checked />
					</div>
					<div slot='status' slot-scope="text,record,index" v-else>
						<a-switch checked-children="开" un-checked-children="关" @click='qh($event,record.id)' />
					</div>

					<span slot="edit" slot-scope="text, record">
						<a-space size="small">
							<a-tooltip placement="left">
								<template slot="title">
									<span>删除</span>
								</template>
								<a-popconfirm title="是否要删除?" ok-text="是" cancel-text="否" @confirm="del(record.id)">
									<a-icon class="pointer" type="delete" />
								</a-popconfirm>
							</a-tooltip>
							<span>|</span>

							<a-tooltip placement="top">
								<template slot="title">
									<span>编辑</span>
								</template>
								<a-icon class="pointer" type="form"
									@click="showEdit(record.id,record.name, record.domain,record.mail,record.gjlx,record.gjfz)" />
							</a-tooltip>
							<span>|</span>
							<a-tooltip placement="top">
								<template slot="title">
									<span>详情</span>
								</template>
								<a-icon class="pointer" type="file-text"
									@click="showsee(record.id,record.name, record.domain,record.mail,record.gjlx,record.gjfz)" />
							</a-tooltip>

						</a-space>
					</span>

				</a-table>

			</a-row>
		</div>

	</div>
</template>

<script>
	const columns = [
		//     {
		//     title: '序号',
		//     dataIndex: 'id',
		//     sorter: (a, b) => a.id - b.id,
		//     width: '10%',
		//
		// },

		{
			title: '任务名称',
			dataIndex: 'name',
			// width: '20%',
		},

		{
			title: '域名信息',
			dataIndex: 'domain',
			scopedSlots: {
				customRender: 'domain'
			},

		},
		{
			title: '执行状态',
			dataIndex: 'status',
			scopedSlots: {
				customRender: 'status'
			},
			filters: [{
					text: '未执行',
					value: '0'
				},
				{
					text: '正在执行',
					value: '1'
				},
			],
			filterMultiple: false,
			onFilter: (value, record) => record.status.indexOf(value) === 0,
			// width: '20%',
		},
		
		{
			title: '操作',
			dataIndex: 'edit',
			scopedSlots: {
				customRender: 'edit'
			},

		},
	];
	// const data = [{
	//     'id': '1',
	//     'name': 'Scan',
	//     'domain': 'www.baidu.com',
	//     'status': 0
	//
	// },
	//     {
	//         'id': '2',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '3',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '4',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '25',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '6',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '7',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '8',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '9',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '10',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	//     {
	//         'id': '11',
	//         'name': 'Scan',
	//         'domain': 'www.baidu.com',
	//         'status': 1
	//
	//     },
	// ]
	export default {
		data() {
			return {
				visible: false,
				visible_edit: false,
				visible_see: false,
				data: [],
				// total: '',
				pagination: {
					// total: this.total,
					defaultPageSize: 10,
					// pageSize.sync:'5'
					showQuickJumper: true,
					showSizeChanger: true,
					pageSizeOptions: ['10', '20', '30', '40', '50', '100'],

				},
				loading: false,
				columns,
				formLayout: 'horizontal',
				form: this.$form.createForm(this),
				formupdate: this.$form.createForm(this),

				scan: 0,

				id: 0,
				plainOptions: ['可用性', '完整性'],
				value: [],
				xsnumber: '80'


			}
		},
		mounted() {
			this.onLoad()


		},
		methods: {
			onSearch(value) {

				this.loading = true
				this.$http.post('/api/task/search', {
					'content': value,
				}).then((res) => {
					console.log(res)
					if (res.data.status == 'success') {

						this.$message.success('查询成功')
						this.loading = false;
						this.data = res.data.res;
					} else {
						this.loading = false;
						this.$message.error('查询失败')
					}

				})

				console.log(value)


			},
			afterVisibleChange(val) {
				console.log('visible', val);
			},
			showDrawer() {
				this.visible = true;
				console.log(this.form)

			},
			onChange() {
				console.log(this.value);
			},
			onClose() {
				this.visible = false;
			},
			onClose_edit() {
				this.visible_edit = false;
			},
			qh(event, id) {
				console.log(event, id)
				if (event) {
					this.$http.post('/api/task/scan_status', {
						'id': id,
						'status': 1
					}).then((res) => {
						if (res.data.status == 'success') {
							this.$message.success('修改成功')

							this.onLoad();
						} else {
							this.$message.error('修改失败')
						}

					})
				} else {
					this.$http.post('/api/task/scan_status', {
						'id': id,
						'status': 0
					}).then((res) => {
						if (res.data.status == 'success') {
							this.$message.success('修改成功')

							this.onLoad();
						} else {
							this.$message.error('修改失败')
						}

					})
				}
			},
			lj(event) {
				if (event) {
					this.scan = 1
				} else {
					this.scan = 0
				}
			},
			handleSubmit(e) {
				e.preventDefault();
				this.form.validateFields((err, values) => {
					if (!err) {
						this.$http.post('/api/task/add', {
							'name': values.name,
							'domain': values.domain,
							'mail': values.email,
							'scan': this.scan,
							'gjlx':this.value,
							'gjfz':this.xsnumber
						}).then((res) => {
							if (res.data.status == 'success') {
								this.$message.success('添加成功')
								this.visible = false;
								this.onLoad();
							}

						})
					}
				});
			},
			onLoad() {
				this.loading = true;
				this.$http.get('/api/task/add').then((res) => {
					console.log(res);
					this.loading = false;
					this.data = res.data.res;
					// this.total = int(res.data.total)

				})
			},
			showEdit(id, namex, domainx, emailx,gjlx,gjfz) {
				this.value=gjlx.replace('[','').replace(']','').replace("'",'').replace("'",'').replace("'",'').replace("'",'').replace(' ','').split(',')
				this.xsnumber=gjfz
				
				setTimeout(() => {
					this.formupdate.setFieldsValue({
						name: namex,
						domain: domainx,
						email: emailx,
					});
				}, 100);

				this.visible_edit = true;
				//用户更新提交主键值
				this.id = id


			},
			showsee(id, namex, domainx, emailx,gjlx,gjfz) {
				this.value=gjlx.replace('[','').replace(']','').replace("'",'').replace("'",'').replace("'",'').replace("'",'').replace(' ','').split(',')
				this.xsnumber=gjfz
				setTimeout(() => {
					this.formupdate.setFieldsValue({
						name: namex,
						domain: domainx,
						email: emailx,
					});
				}, 100);

				this.visible_see = true;


			},
			onClose_see() {
				this.visible_see = false;
			},
			Editupdate(e) {
				e.preventDefault();
				this.formupdate.validateFields((err, values) => {
					if (!err) {
						this.$http.post('/api/task/edit', {
							'id': this.id,
							'name': values.name,
							'domain': values.domain,
							'mail': values.email,
							'gjlx':this.value,
							'gjfz':this.xsnumber
						}).then((res) => {
							if (res.data.status == 'success') {
								this.$message.success('更新成功')
								this.visible_edit = false;
								this.onLoad();
							}

						})
					}
				});
			},
			del(id) {
				console.log(id)
				this.$http.post('/api/task/del', {
					'id': id,
				}).then((res) => {
					if (res.data.status == 'success') {
						this.$message.success('删除成功')
						this.onLoad();
					};
				})
			}


		},


	}
</script>

<style scoped>
	.creat {
		background: #edeff3;
		border: 8px solid #edeff3;
		border-radius: 5px;
	}

	.cj {
		float: right;
	}

	#list {
		margin-top: 10px;
	}

	.ant-table-body {
		background: #edeff3;
	}

	.header {
		background-color: #edeff3;
	}

	.butt {
		margin-left: 20px;
	}
</style>
