<template>
	<div class='login'>
		<div class="login_box">
			<!-- 头像 -->
			<div class="box_img">
				<img src="../assets/logo.png" alt="">
			</div>

			<!-- 用户名 -->
			<a-form id="components-form-demo-normal-login" class="loginFrom" :form='form' @submit="login">
				<a-form-item>
					<a-input  v-decorator="[
			          'userName',
			          { rules: [{ required: true, message: '请输入用户!' }] },
			        ]" placeholder="Username">
						<a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
					</a-input>
				</a-form-item>
				<a-form-item>
					<a-input v-decorator="[
			          'password',
			          { rules: [{ required: true, message: '请输入密码!' }] },
			        ]" type="password" placeholder="Password" has-feedback>
						<a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
					</a-input>
				</a-form-item> 
				<a-form-item>


					<a-button type="primary" html-type="submit" class="login-form-button" block>
						登 录
					</a-button>


				</a-form-item>
			</a-form>
		</div>

	</div>
</template>

<script>
	
				
	export default {
		name: 'Login',
		data() {
			return {
				form: this.$form.createForm(this),

				
			}
		},
		methods: {
			login(e) {
				e.preventDefault();
				this.form.validateFields((err, values) => {
					if (!err) {
						
						this.$http.post('/login/', {
							username: values.userName,
							password: values.password
						}).then((res) => {
							
							if (res.data.status === 'success') {
								this.$message.success('登录成功',1)
								this.$store.commit('loginedit',res.data.user)
								window.sessionStorage.setItem('token',res.data.token)
								this.$router.push('/dashboard')
							} else {
								this.$message.error('登录失败')
							}

						})

					}
				});
			},
			
		},
		

	}
</script>

<style scoped>
	.login {
		height: 100%;
	}

	.login_box {
		width: 400px;
		height: 300px;
		background-color: #ffffff;
		border-radius: 3px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);


	}

	.box_img {
		height: 130px;
		width: 130px;
		border: 1px solid #eee;
		border-radius: 50%;
		padding: 10px;
		box-shadow: 0 0 10px #ddd;
		position: absolute;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #fff;

	}

	img {
		width: 100%;
		height: 100%;
		border-radius: 50%;
		background-color: #eee;
	}


	.loginFrom {
		position: absolute;
		transform: translate(0%, -20%);
		bottom: 0;
		width: 100%;
		padding: 0 20px;
		box-sizing: border-box;

	}
</style>
