<template>
	<div class='bj'>
		<a-tabs default-active-key="1" tab-position="left" size='large'>
			<a-tab-pane key="1" tab="数据清理">
				<div class='bk'>
					<a-row>
						<a-alert message="警告" description="数据一经删除，不可恢复." banner />
					</a-row>
					<a-list item-layout="horizontal">
						<a-list-item>
							<a-list-item-meta title="清空源码指纹" description="清空网站监测任务初始留存的源码"></a-list-item-meta>
							<a-popconfirm slot="actions" title="是否要清空剧本数据?" ok-text="是" cancel-text="否"
								@confirm="del(1)">
								<a-button type="danger" icon="delete">清空源码</a-button>
							</a-popconfirm>
						</a-list-item>
						<a-list-item>
							<a-list-item-meta title="清空快照指纹" description="清空网站监测任务初始留存的图片"></a-list-item-meta>
							<a-popconfirm slot="actions" title="是否要清空日记数据?" ok-text="是" cancel-text="否"
								@confirm="del(2)">
								<a-button type="danger" icon="delete">清空快照</a-button>
							</a-popconfirm>
						</a-list-item>
						<a-list-item>
							<a-list-item-meta title="清空篡改源码" description="清空网站监测任务监测到的篡改源代码"></a-list-item-meta>
							<a-popconfirm slot="actions" title="是否要清空报告数据?" ok-text="是" cancel-text="否"
								@confirm="del(3)">
								<a-button type="danger" icon="delete">篡改源码</a-button>
							</a-popconfirm>
						</a-list-item>
						<a-list-item>
							<a-list-item-meta title="清空篡改快照" description="清空网站监测监测篡改后保存的图片"></a-list-item-meta>
							<a-popconfirm slot="actions" title="是否要清空变量数据?" ok-text="是" cancel-text="否"
								@confirm="del(4)">
								<a-button type="danger" icon="delete">篡改快照</a-button>
							</a-popconfirm>
						</a-list-item>
						<a-list-item>
							<a-list-item-meta title="清空对比报告" description="清空对比报告将无法查看网站的源码对比"></a-list-item-meta>
							<a-popconfirm slot="actions" title="是否要清空缓存?" ok-text="是" cancel-text="否" @confirm="del(5)">
								<a-button type="danger" icon="delete">对比报告</a-button>
							</a-popconfirm>
						</a-list-item>
					</a-list>
				</div>

			</a-tab-pane>
			<a-tab-pane key="2" tab="监测频率" force-render>
				<div class='bk'>
					<a-row>
						<a-alert message="提示" description="监测不建议过快,否则出现进程锁定" banner type='info' />
					</a-row>
					<a-divider />
					<div style="margin-top:20px;">
						<div class='kuang'>
							<div class='bt'>
								网站可用性监测率
							</div>
							<a-row>
								<a-col :span="12">
									<a-slider v-model="keyong" :min="10" :max="100" @afterChange='ky' />
								</a-col>
								<a-col :span="4">
									<a-input-number v-model="keyong" :min="1" :max="100" style="marginLeft: 16px" /> 秒
								</a-col>
							</a-row>
						</div>
						<a-divider />
						<div class='kuang'>
							<div class='bt'>
								网站篡改性监测率
							</div>
							<a-row>
								<a-col :span="12">
									<a-slider v-model="wanzheng" :min="1" :max="100" @afterChange='wz' />
								</a-col>
								<a-col :span="4">
									<a-input-number v-model="wanzheng" :min="1" :max="100" style="marginLeft: 16px" /> 分
								</a-col>
							</a-row>
						</div>
					</div>

				</div>


			</a-tab-pane>
			<a-tab-pane key="3" tab="告警设置">
				<div class="bk">
					<a-row>
						<a-alert message="提示" description="发送邮件服务器配置" banner type='info' />
					</a-row>
					<a-divider />
					<div>
						<a-row>
							<a-form :form="formmail">
								<a-form-item v-bind="formItemLayout" label="发件人邮箱">
									<a-input v-decorator="[
								          'email',
								          {
								            rules: [
								             {
								                type: 'email',
								                message: '请输入有效的发件人邮箱!',
								              },
								              {
								                required: true,
								                message: '请输入发件人邮箱!',
								              },
								            ],
								          },
								        ]" />
								</a-form-item>
								<a-form-item v-bind="formItemLayout" label="发件人密码">
									<a-input v-decorator="[
								          'password',
								          {
								            rules: [
								             
								              {
								                required: true,
								                message: '输入邮箱密码!',
								              },
								            ],
								          },
								        ]" type='password' />
								</a-form-item>
								<a-form-item v-bind="formItemLayout" label="邮件服务器">
									<a-input v-decorator="[
								          'server',
								          {
								            rules: [
								    
								              {
								                required: true,
								                message: '请输入邮件服务器地址!',
								              },
								            ],
								          },
								        ]" />
								</a-form-item>
								<a-form-item v-bind="formItemLayout" label="SMTP端口">
									<a-input v-decorator="[
								          'port',
								          {
								            rules: [
								        
								              {
								                required: true,
								                message: '请输入发件服务器端口!',
								              },
								            ],
								          },
								        ]" />
								</a-form-item>
								<a-form-item v-bind="formItemLayout" label="邮件协议">
									<a-select :default-value="protype" style="width: 80px" @change="xieyi" :key='protype'>
										<a-select-option value="none">
											none
										</a-select-option>
										<a-select-option value="tsl">
											tsl
										</a-select-option>
										<a-select-option value="ssl">
											ssl
										</a-select-option>
									</a-select>
									
									<a-button   @click='showmail' style='margin-left: 5px;'>配置查看</a-button>
									<span style='float: right;' >
										<a-button type="primary" @click='Tjmail'>保存</a-button>
									</span>
									
								</a-form-item>

							</a-form>
						</a-row>
						



					</div>
				</div>


			</a-tab-pane>
		</a-tabs>
	</div>

</template>

<script>
	export default {
		data() {
			return {
				keyong: 10,
				wanzheng: 1,
				formItemLayout: {
					labelCol: {
						xs: {
							span: 24
						},
						sm: {
							span: 3
						},
					},
					wrapperCol: {
						xs: {
							span: 24
						},
						sm: {
							span: 8
						},
					},
				},
				formmail: this.$form.createForm(this),
				protype: 'none',
				

			}
		},
		methods: {
			ky(value) {
				console.log(value)
				this.$http.post('/api/task/time/update/jc', {
					time: value,
				}).then((res) => {
					if (res.data.status === 'success') {
						this.$message.success('更新成功')


					}

				})

			},
			wz(value) {
				this.$http.post('/api/task/time/update/wz', {
					time: value,
				}).then((res) => {
					if (res.data.status === 'success') {
						this.$message.success('更新成功')

					}

				})
			},
			del(id) {
				this.$http.post('/api/del', {
					id: id,
				}).then((res) => {
					if (res.data.status === 'success') {
						this.$message.success('删除成功')

					}

				})
			},
			onLoad() {
				this.$http.get('/api/task/time').then((res) => {
					this.keyong = parseInt(res.data.res[0])
					this.wanzheng = parseInt(res.data.res[1])

				})
				
				
			},
			Tjmail(e) {
				e.preventDefault();
				this.formmail.validateFields((err, values) => {
					if (!err) {
						this.$http.post('/api/mail/', {
							email: values.email,
							password: values.password,
							server: values.server,
							port: values.port,
							protype: this.protype
						}).then((res) => {
							if (res.data.status === 'success') {
								this.$message.success('添加成功')

							}
						})
					}
				});
			},
			xieyi(value) {
				console.log(value)
				this.protype = value
			},
			showmail() {
				
				this.$http.get('/api/mail/').then((res) => {
					if (res.data.status === 'success') {
						
						setTimeout(() => {
							this.formmail.setFieldsValue({
								email:res.data.res.username,
								password: res.data.res.password,
								port: res.data.res.port,
								server: res.data.res.server,
								
							});
							
							
						},100)
						
						this.protype = res.data.res.protype
						
						
					}

				})
				

			},
		},
		mounted() {
			this.onLoad()
		

		},





	}
</script>

<style scoped>
	.bj {

		border-radius: 5px;


	}

	.bk {
		margin: 20px;
		border-radius: 5px;
		border: solid 1px;
		padding: 20px
	}

	.bt {

		padding: 10px;
		background-color: #1890ff;
		width: fit-content;
		border-radius: 4px;
		color: white;
	}
</style>
